.multi-range-container {
  max-width: 400px;
  border: 1px solid #000;
  background-color: rgba(0, 0, 0, 0.25);
  padding: 20px;
}

.multi-range {
  position: relative;
  height: 50px;
  display: block;
  width: 100%;

  input[type=range] {
     position: absolute;
  }
}

.range-color {
  background-color: #F8F8F9;
  border-radius: 50px;
  width: 100%;
  display: block;
  height: 10px;
  position: absolute;
  z-index: 10;
}
body {
  padding: 4em;
}

input[type=range] {
  box-sizing: border-box;
  appearance: none;
  width: 100%;
  height: 10px;
  background-color: #b2b2b2;
  border-radius: 50px;
  margin: 0;
  border: 0;
  outline: none;
  background-size: 100% 2px;
  pointer-events: none;
  box-shadow: inset 0 0 4px #000000;
  border: 1px solid #000;
  -webkit-appearance: none;
  width: 100%;
  &:active,
  &:focus {
     outline: none;
  }
  &::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 28px;
    width: 28px;
    border-radius: 28px;
    border: solid 1px #b2b2b2;
    background-color: #fff;
    position: relative;
    z-index: 50;
    cursor: pointer;
    appearance: none;
    pointer-events: all;
    box-shadow: 0 1px 4px 0.5px rgba(0, 0, 0, 0.25);
  }
  &::-ms-track {
    width: 100%;
    cursor: pointer;
    background: transparent; 
    border-color: transparent;
    color: transparent;
    height: 8.4px;
    cursor: pointer;
    border-width: 16px 0;
  }
  &::-moz-range-thumb {
    height: 28px;
     width: 28px;
     border-radius: 28px;
     border: solid 1px #b2b2b2;
     background-color: #fff;
     position: relative;
     z-index: 50;
     cursor: pointer;
     appearance: none;
     pointer-events: all;
     box-shadow: 0 1px 4px 0.5px rgba(0, 0, 0, 0.25);
   }
   &::-ms-thumb {
    height: 28px;
     width: 28px;
     border-radius: 28px;
     border: solid 1px #b2b2b2;
     background-color: #fff;
     position: relative;
     z-index: 10000;
     cursor: pointer;
     appearance: none;
     pointer-events: all;
     box-shadow: 0 1px 4px 0.5px rgba(0, 0, 0, 0.25);
   }
   &::-ms-fill-lower {
    background: #2a6495;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
   }
   &::-ms-fill-upper {
    background: #3071a9;
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
   }
}





